import { workoutListApiEndpoint } from '../config';
import { ResponseJsonOrThrow } from './ServiceHelper';

const RetrieveSessons = async() => {
  const data = await fetch(`${workoutListApiEndpoint}/workoutsessions`)
  return ResponseJsonOrThrow(data);
}

const RetrieveSessionById = async(id) => {
  const data = await fetch(`${workoutListApiEndpoint}/workoutsessions/${id}`)
  return ResponseJsonOrThrow(data);
}

const StopSession = async(session) => {
  const body = JSON.stringify(session);
  const data = await fetch(`${workoutListApiEndpoint}/workoutsessions/${session.id}`,
    {
      method: 'post',
      body: body,
      headers: { 'Content-type': 'application/json' }
    });
  return ResponseJsonOrThrow(data);
}

const CreateSession = async(session) => {
  const body = JSON.stringify(session);
  const data = await fetch(`${workoutListApiEndpoint}/workoutsessions`,
    {
      method: 'post',
      body: body,
      headers: { 'Content-type': 'application/json' }
    });
  return ResponseJsonOrThrow(data);
}

export { RetrieveSessons, RetrieveSessionById, StopSession, CreateSession };



