import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Main } from './main';
import { Header } from './header';

class App extends Component {
  constructor() {
    super();
    this.state = {
      hasError: false
    };
  }

  static getDerivedStateFromError(error) {
    // This is an error Boundry for the entire application
    return { hasError: true };
  }

  render() {
    let data;
    if(this.state.hasError) {
      return <div>
          <div className="alert alert-danger" role="alert">Sorry, something went really wrong here, please try again.</div>
        </div>
    }
    return (
      <div>
        <Header />
        <Main />
      </div>
    )
  }
}

export default App;



