import { RetrieveSessionById, StopSession } from '../services/WorkoutSessionService';

const LOAD_SESSION_START = 'LOAD_SESSION_START';
const LOAD_SESSION_ERROR = 'LOAD_SESSION_ERROR';
const LOAD_SESSION_SUCCESS = 'LOAD_SESSION_SUCCESS';
const STOP_SESSION_START = 'STOP_SESSION_START';
const STOP_SESSION_ERROR = 'STOP_SESSION_ERROR';
const STOP_SESSION_SUCCESS = 'STOP_SESSION_SUCCESS';

export const INITIAL_STATE = {
  session: {},
  loadingSession: false
};

export function loadSession(payload) {
  return async (dispatch) => {
    dispatch({ type: LOAD_SESSION_START, payload })
    try {
      const data = await RetrieveSessionById(payload.sessionId)
      dispatch({ type: LOAD_SESSION_SUCCESS, payload: data })
    } catch(err) {
      const errorMessage = `An error occurred while loading Session: ${payload.sessionId}, please try again.`
      dispatch({ type: LOAD_SESSION_ERROR, payload: errorMessage })
    }
  }
};

export function stopSession(payload) {
  return async (dispatch) => {
    dispatch({ type: STOP_SESSION_START, payload })
    try {
      const data = await StopSession(payload.session)
      dispatch({ type: STOP_SESSION_SUCCESS, payload: data })
    } catch(err) {
      const errorMessage = `An error occurred while stopping Session: ${payload.sessionId}, please try again.`
      dispatch({ type: STOP_SESSION_ERROR, payload: errorMessage })
    }
  }
};

export const ACTION_HANDLERS = {
  [LOAD_SESSION_START]: (state, action) => {
    return {
      ...state,
      loadingSession: true,
      error: undefined
    }
  },
  [LOAD_SESSION_ERROR]: (state, action) => {
    return {
      ...state,
      loadingSession: false,
      error: action.payload
    }
  },
  [LOAD_SESSION_SUCCESS]: (state, action) => {
    return {
      ...state,
      loadingSession: false,
      session: { ...action.payload }
    }
  },
  [STOP_SESSION_START]: (state, action) => {
    return {
      ...state,
      stoppingSession: true,
      error: undefined
    }
  },
  [STOP_SESSION_ERROR]: (state, action) => {
    return {
      stoppingSession: false,
      error: action.payload,
      ...state
    }
  },
  [STOP_SESSION_SUCCESS]: (state, action) => {
    return {
      ...state,
      stoppingSession: false,
      session: { ...action.payload }
    }
  }
}



