import { workoutListApiEndpoint } from '../config';
import { ResponseJsonOrThrow } from './ServiceHelper';

const RetrieveExercises = async() => {
  const data = await fetch(`${workoutListApiEndpoint}/exercises`);
  return ResponseJsonOrThrow(data);
}

const RetrieveExerciseById = async id => {
  const data = await fetch(`${workoutListApiEndpoint}/exercises/${id}`);
  return ResponseJsonOrThrow(data);
}

const CreateExercise = async(exercise) => {
  const body = JSON.stringify(exercise);
  const data = await fetch(`${workoutListApiEndpoint}/exercises`,
    {
      method: 'post',
      body: body,
      headers: { 'Content-type': 'application/json' }
    });
  return ResponseJsonOrThrow(data);
}

const UpdateExercise = async(exercise) => {
  const body = JSON.stringify(exercise);
  const response = await fetch(`${workoutListApiEndpoint}/exercises/${exercise.id}`,
    {
      method: 'post',
      body: body,
      headers: { 'Content-type': 'application/json' }
    }
  );
  return ResponseJsonOrThrow(response);
}

const DeleteExercise = async(id) => {
  const data = await fetch(`${workoutListApiEndpoint}/exercises/${id}`,
    {
      method: 'delete',
      headers: { 'Content-type': 'application/json' }
    });
  return ResponseJsonOrThrow(data);
}

export { RetrieveExerciseById, CreateExercise, UpdateExercise, RetrieveExercises, DeleteExercise };



