import { workoutListApiEndpoint } from '../config';
import { ResponseJsonOrThrow } from './ServiceHelper';

const RetrieveWorkouts = async() => {
  const data = await fetch(`${workoutListApiEndpoint}/workouts`)
  return ResponseJsonOrThrow(data);
}

const RetrieveWorkoutById = async(id) => {
  const data = await fetch(`${workoutListApiEndpoint}/workouts/${id}`)
  return ResponseJsonOrThrow(data);
}

export { RetrieveWorkouts, RetrieveWorkoutById };



