import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { SessionRoute, WorkoutListRoute, ExerciseRoute } from '../../services/RouteService';

const ExerciseList = (props) => {
  const exercises = props.exercises;
  const listItems = exercises.map((exercise) =>
    <Exercise key={exercise.id}
      workoutId={props.workoutId}
      sessionId={props.sessionId}
      exercise={exercise}
      editMode={props.editMode}
      handleRemoveClick={props.handleRemoveClick}
    />
  );

  return (
    <div>
      {listItems}
    </div>
  );
};

export class Exercise extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editExercise: false
    }

    this.editExercise = this.editExercise.bind(this);
  }

  editExercise() {
    this.setState({
      editExercise: true
    })
  }

  referrer() {
    let referrerPath = `${WorkoutListRoute()}/${this.props.workoutId}`;
    if (this.props.sessionId !== undefined && this.props.sessionId.length > 0) {
      referrerPath = `${SessionRoute()}${this.props.sessionId}`;
    }
    return `${referrerPath}#${this.props.exercise.groupId}`;
  }

  render(){
    const exercise = this.props.exercise;
    if (this.state.editExercise) {
      const url = {
        pathname: `${ExerciseRoute()}/${exercise.id}`,
        referrer: this.referrer()
      };

      return <Redirect push to={url} />
    }
    return (
      <div key={exercise.id} onClick={this.editExercise.bind(this)}>
        <div className="d-flex flex-row exercise-container">
          <div className="p-1 mr-1 left-column">
            {exercise.name}
          </div>
          <div className="p-1 text-danger">
            {exercise.weight}
          </div>
          <div className="ml-auto p-1">
            {exercise.set}
          </div>
          { this.props.editMode &&
            <RemoveButton handleRemoveClick={this.props.handleRemoveClick} id={exercise.id}/>
          }
        </div>
      </div>
    );
  }
};

const RemoveButton = (props) => {
  return (
    <div className="ml-auto p-1">
      <button type="button" onClick={() => { props.handleRemoveClick(props.id) }}>
        <span className="text-danger"><i className="fas fa-times"></i></span>
      </button>
    </div>
  )
}

export default ExerciseList;



