const elapsedTimeFromStart = startTime => {
  let t = Date.parse(new Date()) - Date.parse(startTime);
  if (t < 0) {
    t = 0;
  }

  return timeForDuration(t);
}

const duration = (start, end) => {
  let t = Date.parse(end) - Date.parse(start);
  if (t < 0) {
    t = 0;
  }

  return t;
}

const timeForDuration = duration => {
  const seconds = Math.floor( (duration/1000) % 60 );
  const minutes = Math.floor( (duration/1000/60) % 60 );
  const hours = Math.floor( (duration/(1000*60*60)) % 24 );
  const days = Math.floor( duration/(1000*60*60*24) );

  let timer = {
    'seconds': seconds,
    'minutes': minutes,
    'hours': hours,
    'days': days,
  }
  return timer;
}

const formattedTimer = timer => {
  let secondsString = `0${timer.seconds}s`.slice(-3);

  let minutesString = '';
  if (timer.minutes > 0) {
    minutesString = `0${timer.minutes}m `.slice(-4);
  }

  let hoursString = '';
  if (timer.hours > 0) {
    hoursString = `${timer.hours}h `;
  }

  let dayString = '';
  if (timer.days > 0) {
    dayString = `${timer.days} days `;
  }

  return `${dayString}${hoursString}${minutesString}${secondsString}`;
}

const DurationInSeconds = (start, end) => {
  return duration(start, end)/1000;
}

const FormattedElapsedTime = startTime => {
  return formattedTimer(elapsedTimeFromStart(startTime));
}

const FormattedDuration = (start, end) => {
  return formattedTimer(timeForDuration(duration(start, end)));
}

export { FormattedElapsedTime, FormattedDuration, DurationInSeconds };



