import React, { Component } from 'react';
import { connect } from 'react-redux';

import { RoundedHeading } from '../roundedHeading';
import { Workout } from '../workout/workout';
import { ElapsedTime } from '../timer/elapsedTime';
import LoadingSpinner from '../loadingSpinner';
import { loadSession, stopSession } from '../../store/actions'

const mapStateToProps = state => {
  return { 
    loadingSession: state.loadingSession,
    stoppingSession: state.stoppingSession,
    session: state.session,
    errorMessage: state.error
  };
}

function mapDispatchToProps(dispatch) {
  return {
    loadSession: session => dispatch(loadSession(session)),
    stopSession: session => dispatch(stopSession(session))
  }
}

class ConnectedSession extends Component {
  constructor(props){
    super(props);
    this.sessionId = props.match.params.id;
    let hash = '';
    if(props.location && props.location.hash) {
      hash = props.location.hash;
    }
    this.state = {
      hash,
      loading: true
    }
    this.handleSessionStopClick = this.handleSessionStopClick.bind(this);
  }

  componentDidMount(){
    const sessionId = this.sessionId;
    this.props.loadSession({ sessionId });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.loadingSession !== this.props.loadingSession) {
      this.setState({
        loading: this.props.loadingSession
      })
    }

    if (prevProps.stoppingSession !== this.props.stoppingSession) {
      this.setState({
        loading: this.props.stoppingSession
      })
    }
  }

  handleSessionStopClick(event) {
    event.preventDefault();
    const session = this.props.session;
    session.endDate = new Date();
    this.props.stopSession({ session });
  }

  dateFormatter = date => {
    const options = {
      year: 'numeric', month: 'numeric', day: 'numeric',
      hour: 'numeric', minute: 'numeric', second: 'numeric'
    };
    const dateFormatter = new Intl.DateTimeFormat('en-US', options);
    return dateFormatter.format(new Date(date));
  }

  render(){
    let data;
    if (this.state.loading) {
      data = <div>
          <RoundedHeading headingText='Session' />
          <LoadingSpinner type='spin' color='green' />
        </div>
    } else if(this.props.errorMessage) {
      data = <div>
          <RoundedHeading headingText='Session' />
            <div className="alert alert-danger" role="alert">{this.props.errorMessage}</div>
          </div>
    } else {
      data = <div>
          <RoundedHeading headingText={this.props.session.sessionType}
            subHeadingText={'Started: ' + this.dateFormatter(this.props.session.startDate)} />
          <SessionTimer session={this.props.session} handleClick={this.handleSessionStopClick}/>
          <Workout id={this.props.session.workoutId} sessionId={this.sessionId} scrollToGroup={this.state.hash}/>
          <SessionTimer session={this.props.session} handleClick={this.handleSessionStopClick}/>
        </div>
    }
    return (
      <div>
        {data}
      </div>
    )
  }
}

const SessionTimer = props => {
  let displayTimer = false;
  if (props.session.endDate === null) {
    displayTimer = true;
  }

  if (displayTimer) {
    return (
      <button className="btn btn-warning" onClick={props.handleClick}>
        Session time: <ElapsedTime startTime={props.session.startDate}/>
      </button>
    )
  } else {
    return <div></div>
  }

}

export const Session = connect(mapStateToProps, mapDispatchToProps)(ConnectedSession);


