import React, { Component } from 'react';

import LoadingSpinner from '../loadingSpinner';
import { RoundedHeading } from '../roundedHeading';
import { RetrieveSessons } from '../../services/WorkoutSessionService';
import { WorkoutDurationGraph } from './workoutDurationGraph';

export class GraphList extends Component {
    constructor() {
        super();
        this.state = {
            loading: true
        };
    }

    async componentDidMount() {
        try {
            const data = await RetrieveSessons();
            const abWorkouts = data.filter(workout => workout.workoutId === 1 );
            const bicepWorkouts = data.filter(workout => workout.workoutId === 2 );
            const legWorkouts = data.filter(workout => workout.workoutId === 3 );
            this.setState({
                loading: false,
                abWorkouts,
                legWorkouts,
                bicepWorkouts
            });
        } catch(err) {
            this.setState({
                error: true,
                errorMessage: `An error occurred while loading Graph data, please try again.`,
                loading: false
              });
        }
    }

    render() {
        let data;
        if(this.state.loading) {
            data = <LoadingSpinner type='spin' color='green' />
        } else if(this.state.error) {
            data = <div className="alert alert-danger" role="alert">
                    {this.state.errorMessage}
                    </div>
        } else {
            data = <div>
                <div className="my-3 p-3 bg-white rounded shadow-sm">
                    <h6 className="border-bottom border-gray pb-2 mb-0">Abs/Chest/Forearm Workout Duration</h6>
                    <WorkoutDurationGraph workouts={this.state.abWorkouts} uniqueGraphName='abs'/>
                </div>
                <div className="my-3 p-3 bg-white rounded shadow-sm">
                    <h6 className="border-bottom border-gray pb-2 mb-0">Shoulder/Leg Workout Duration</h6>
                    <WorkoutDurationGraph workouts={this.state.legWorkouts} uniqueGraphName='legs'/>
                </div>
                <div className="my-3 p-3 bg-white rounded shadow-sm">
                    <h6 className="border-bottom border-gray pb-2 mb-0">Back/Bicep/Neck Workout Duration</h6>
                    <WorkoutDurationGraph workouts={this.state.bicepWorkouts} uniqueGraphName='bicep'/>
                </div>
            </div>
        }
        return (
            <div>
                <RoundedHeading headingText="Graphs:" />
                {data}
            </div>
        )
    }
}


