import React from 'react';

export const RoundedHeading = (props) => {
  return (
    <div className="d-flex align-items-center p-3 my-3 text-white-50 bg-green rounded shadow-sm">
      <div className="lh-100">
        <h3 className="mb-0 text-white lh-100">
          {props.headingText}
          <EditButton editable={props.editable} handleEditClick={props.handleEditClick} editMode={props.editMode}/>
        </h3>
        <span>{props.subHeadingText}</span>
      </div>
    </div>
  );
}

const EditButton = (props) => {
  let buttonText = 'Edit';
  let buttonType = 'btn btn-secondary';
  if(props.editMode) {
    buttonText = 'Done';
    buttonType = 'btn btn-primary';
  }
  if(props.editable) {
    return (
      <button type="button" className={buttonType} onClick={props.handleEditClick}>{buttonText}</button>
    )
  } else {
    return <div></div>
  }
}



