import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import { SessionList } from './components/session/sessionList';
import { WorkoutList } from './components/workout/workoutList';
import { Workout } from './components/workout/workout';
import { ExerciseForm } from './components/exercise/exerciseForm';
import { SessionForm } from './components/session/sessionForm';
import { Session } from './components/session/session';
import { GraphList } from './components/graphs/graphList';
import {
  RootRoute, SessionRoute, SessionListRoute, NewSessionRoute, WorkoutListRoute, ExerciseRoute, NewExerciseRoute, GraphRoute
} from './services/RouteService';

export class Main extends Component {
  render() {
    const sessionRoute = SessionRoute() + ':id';
    const workoutRoute = WorkoutListRoute() + '/:id';
    const exerciseRoute = ExerciseRoute() + '/:id';

    return (
      <main role="main" className="container">
        <Route exact path={RootRoute()} component={props => <Home {...props} />}/>
        <Route exact path={SessionListRoute()} component={props => <SessionList {...props} />}/>
        <Route path={NewSessionRoute()} component={props => <SessionForm {...props} />}/>
        <Route path={sessionRoute} component={props => <Session {...props} />}/>
        <Route exact path={WorkoutListRoute()} component={props => <WorkoutList {...props} />}/>
        <Route exact path={workoutRoute} component={props => <Workout {...props} />}/>
        <Route path={exerciseRoute} component={props => <ExerciseForm {...props} />}/>
        <Route path={NewExerciseRoute()} component={props => <ExerciseForm {...props} />}/>
        <Route path={GraphRoute()} component={props => <GraphList {...props} />}/>
      </main>
    )
  }
}

const Home = () => (
  <div className="starter-template">
    <h1>Exercise much?</h1>
    <p className="lead">Lets get to work</p>
  </div>
)



