import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

import { RoundedHeading } from '../roundedHeading';
import { RetrieveWorkouts } from '../../services/WorkoutService';
import { WorkoutListRoute } from '../../services/RouteService';
import LoadingSpinner from '../loadingSpinner';

export class WorkoutList extends Component {
  constructor(){
    super();
    this.state = {
      workouts: [],
      loadSpecificWorkout: -1,
      loading: true
    };

    this.loadWorkout = this.loadWorkout.bind(this);
  }

  async componentDidMount(){
    try {
      const data =  await RetrieveWorkouts();
      let workouts = data.map((workout) => {
        return (
          <WorkoutRow key={workout.id} workout={workout}
            handleWorkoutClick={this.loadWorkout.bind(this, workout.id)}
          />
        )
      });
      this.setState({workouts: workouts, loading: false});
    } catch(err) {
      this.setState({
        error: true,
        errorMessage: `An error occurred while loading Workouts, please try again.`,
        loading: false
      });
    }
  }

  loadWorkout(id, e) {
    this.setState({
      workouts: this.state.workouts,
      loadSpecificWorkout: id,
    });
  }

  render(){
    if(this.state.loadSpecificWorkout > 0) {
      const url = `${WorkoutListRoute()}/${this.state.loadSpecificWorkout}`;
      return (
        <Redirect push to={url} />
      );
    }
    let data;
    if(this.state.loading) {
      data = <LoadingSpinner type='spin' color='green' />
    } else if(this.state.error) {
      data = <div className="alert alert-danger" role="alert">
              {this.state.errorMessage}
            </div>
    } else {
      data = <WorkoutContainer workouts={this.state.workouts} />
    }
    return (
      <div>
        <RoundedHeading headingText="Here are your Workouts:" />
        {data}
      </div>
    )
  }
}

const WorkoutContainer = (props) => {
  const list = props.workouts;

  return (
    <div className="my-3 p-3 bg-white rounded shadow-sm">
      <h6 className="border-bottom border-gray pb-2 mb-0">Workouts</h6>
      {list}
    </div>
  );
}

const WorkoutRow = (props) => {
  const workout = props.workout;

  return (
    <div className="media text-muted pt-3"
    onClick={props.handleWorkoutClick}>
      <div className="media-body pb-3 mb-0 lh-125 border-bottom border-gray">
        <strong className="d-block text-gray-dark">{workout.name}</strong>
      </div>
    </div>
  )
}



