import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

import { RoundedHeading } from '../roundedHeading';
import { RetrieveSessons } from '../../services/WorkoutSessionService';
import { RetrieveWorkouts } from '../../services/WorkoutService';
import { FormattedDuration, FormattedElapsedTime } from '../../services/TimeService';
import { SessionRoute, SessionListRoute, NewSessionRoute } from '../../services/RouteService';
import LoadingSpinner from '../loadingSpinner';

export class SessionList extends Component {
  constructor(){
    super();
    this.state = {
      sessions: [],
      createSession: false,
      loading: true,
    };

    this.handleSessionStartClick = this.handleSessionStartClick.bind(this);
    this.handleSessionRowClick = this.handleSessionRowClick.bind(this);
  }

  // use componentDidMount to do api calls, in order to wait until after mounting
  // to load data
  async componentDidMount(){
    try {
      const workouts = await RetrieveWorkouts();
      const data = await RetrieveSessons();
      let sessions = data.sort((a, b) => new Date(b.startDate) - new Date(a.startDate))
        .map((session) => {
          let workout = workouts.find(workout => workout.id === session.workoutId);
          return (
            <Session key={session.id} session={session} workout={workout}
              handleSessionRowClick={this.handleSessionRowClick.bind(this, session.id)} />
          )
        });

      this.setState({sessions: sessions, loading: false});
    } catch(err) {
      this.setState({
        error: true,
        errorMessage: 'An error occurred while loading Sessions, please try again.',
        loading: false,
      });
    }
  }

  handleSessionStartClick(event) {
    event.preventDefault();
    this.setState({createSession: true});
  }

  handleSessionRowClick(id, event) {
    this.setState({ loadSpecificSession: id });
  }

  render(){
    let data;
    if(this.state.createSession){
      const url = {
        pathname: NewSessionRoute(),
        referrer: SessionListRoute()
      }
      return (
        <Redirect push to={url} />
      );
    } else if(this.state.loadSpecificSession !== undefined && this.state.loadSpecificSession.length > 0) {
      const url = `${SessionRoute()}${this.state.loadSpecificSession}`;
      return <Redirect push to={url} />
    } else if(this.state.loading) {
      data = <LoadingSpinner type='spin' color='green' />
    } else if(this.state.error) {
      data = <div className="alert alert-danger" role="alert">
              {this.state.errorMessage}
            </div>
    } else {
      data = <SessionListContainer sessions={this.state.sessions}
          handleSessionStartClick={this.handleSessionStartClick} />
    }
    return (
      <div>
        <RoundedHeading headingText='Sessions' subHeadingText='Good Job' />
        {data}
      </div>
    );
  }
}

const SessionListContainer = (props) => {
  return (
    <div className="my-3 p-3 bg-white rounded shadow-sm">
      <h6 className="border-bottom border-gray pb-2 mb-0">
        Recent Sessions
        <input type="button" value="Start a new Session" className="btn btn-primary"
        onClick={props.handleSessionStartClick}/>
        Days since last Workout:
      </h6>

      {props.sessions}
      <small className="d-block text-right mt-3">
        <a href="/">All updates</a>
      </small>
    </div>
  );
}

const Session = (props) => {
  const options = {
    year: 'numeric', month: 'numeric', day: 'numeric',
    hour: 'numeric', minute: 'numeric', second: 'numeric'
  };
  const dateFormatter = new Intl.DateTimeFormat('en-US', options);
  let startDate = '';
  if (props.session.startDate !== null && props.session.startDate !== undefined && props.session.startDate.length > 0) {
    startDate = dateFormatter.format(new Date(props.session.startDate));
  }

  let duration = '';
  let endDate = '';
  if (props.session.endDate !== null && props.session.endDate !== undefined && props.session.endDate.length > 0) {
    endDate = dateFormatter.format(new Date(props.session.endDate));
    duration = FormattedDuration(props.session.startDate, props.session.endDate);
  } else {
    duration = FormattedElapsedTime(props.session.startDate);
  }

  const _rowClassNames = (endDate) => {
    let classes = 'media text-muted pt-3';
    if (endDate === undefined || endDate.length < 1) {
      classes = classes + ' ongoingSessionRow';
    }
    return classes;
  }

  return (
    <div className={_rowClassNames(endDate)} onClick={props.handleSessionRowClick}>
      <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
        <strong className="d-block text-gray-dark">{props.session.sessionType}</strong>
        <div>
          <strong>Workout:</strong>{props.workout.name} -
          <strong>Start Date:</strong>{startDate} -
          <strong>Duration:</strong>{duration}
        </div>
      </div>
    </div>
  );
}



