import React, { Component } from 'react';

import { FormattedElapsedTime } from '../../services/TimeService';

export class ElapsedTime extends Component {
  constructor(props){
    super(props);
    this.startTime = new Date();
    if(props.startTime !== undefined){
      this.startTime = props.startTime;
    }
    this.state = {};
    this.timer = 0;
    this.counter = this.counter.bind(this);
  }

  componentDidMount() {
    let elapsedTime = FormattedElapsedTime(this.startTime);
    this.setState({ elapsedTime });

    this.timer = setInterval(this.counter, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  counter() {
    let elapsedTime = FormattedElapsedTime(this.startTime);
    this.setState({ elapsedTime });
  }

  render() {
    return(
      <div>{this.state.elapsedTime}</div>
    );
  }
}



