import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { RootRoute, SessionListRoute, WorkoutListRoute, GraphRoute } from './services/RouteService';

export class Header extends Component {
  render() {
    return (
      <nav className="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
        <Link to={RootRoute()} className="navbar-brand">Time to Exercise!</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarsExampleDefault">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item active">
              <Link to={RootRoute()} className="nav-link">Home</Link>
            </li>
            <li className="nav-item">
              <Link to={WorkoutListRoute()} className="nav-link">Workouts</Link>
            </li>
            <li className="nav-item">
              <Link to={SessionListRoute()} className="nav-link">Sessions</Link>
            </li>
            <li className="nav-item">
              <Link to={GraphRoute()} className="nav-link">Graphs</Link>
            </li>
          </ul>
        </div>
      </nav>
    )
  }
}



