import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

import { CreateSession } from '../../services/WorkoutSessionService';
import { RetrieveWorkouts } from '../../services/WorkoutService';
import { SessionRoute } from '../../services/RouteService';
import LoadingSpinner from '../loadingSpinner';

export class SessionForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      workouts: [],
      workoutType: '0',
      formSubmitted: false,
      formSuccess: true,
      createdSessionId: 0,
      loading: true,
    }

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  async componentDidMount(){
    try {
      const data = await RetrieveWorkouts();
      let workouts = data.map((workout) => {
        return {
          id: workout.id,
          name: workout.name
        }
      });
      this.setState({workouts: workouts, loading: false});
    } catch(err) {
      this.setState({
        error: true,
        errorMessage: 'There was an error retrieving Workouts, please try again.',
        loading: false
      });
    }
  }

  async createSession(sessionId) {
    this.setState({ loading: true });
    try {
      const session = {
        sessionType: sessionId,
        startDate: new Date()
      }
      const data = await CreateSession(session);
      this.setState({
        formSuccess: true,
        formSubmitted: true,
        createdSessionId: data.id,
        loading: false
      });
    } catch(err) {
      this.setState({
        error: true,
        errorMessage: 'There was an error creating your session, please try again.',
        loading: false
      });
    }
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.name;

    this.setState({
      [name]: target.value
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    const selectedSession = this.state.workouts.find(
      workout => workout.id.toString() === this.state.workoutType
    );
    if (selectedSession !== undefined && selectedSession.id > 0) {
      this.createSession(selectedSession.id);
    } else {
      console.log('haven\'t chosen a valid type');
    }
  }

  render(){
    let data;
    if (this.state.formSubmitted && this.state.formSuccess) {
      const url = `${SessionRoute()}${this.state.createdSessionId}`;
      return (
        <Redirect push to={url} />
      )
    } else if(this.state.loading) {
      data = <LoadingSpinner type='spin' color='green' />
    } else if(this.state.error) {
      data = <div className="alert alert-danger" role="alert">{this.state.errorMessage}</div>
    } else {
      data = <form onSubmit={this.handleSubmit}>
        <div className="form-group">
          <label htmlFor="workoutType">Workout Type:</label>
          <select name="workoutType" className="form-control"
          value={this.state.workoutType} onChange={this.handleInputChange}>
            <option value='0'>Choose a Type</option>
            {this.state.workouts.map((workout) => {
              return (<option key={workout.id.toString()} value={workout.id}>{workout.name}</option>)
            })}
          </select>
        </div>
        <input type="submit" value="Start your workout!" className="btn btn-primary"/>
        <input type="button" value="Cancel" className="btn btn-secondary"
            onClick={this.props.history.goBack}/>
      </form>
    }
    return(
      <div>
        {data}
      </div>
    );
  }
}



