import React, { Component } from 'react';

import { DateLineChart } from '../../services/ChartService';
import { DurationInSeconds } from '../../services/TimeService';

export class WorkoutDurationGraph extends Component {
    constructor(props) {
        super(props);
        this.state = {
            workouts: props.workouts,
            graphClass: props.uniqueGraphName,
        };
    }

    componentDidMount() {
        if(this.state.workouts) {
            const durations = this.state.workouts.sort((a, b) => new Date(b.startDate) - new Date(a.startDate))
                .map(workout => {
                    return {
                        date: new Date(workout.startDate),
                        value: Math.floor(DurationInSeconds(workout.startDate, workout.endDate)/60)
                    };
                });
            
            DateLineChart(durations, this.state.graphClass, 'Duration (mins)');
        }
    }

    render() {
        
        return (
            <svg className={this.state.graphClass}></svg>
        );
    }
}


