import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

import { RetrieveExerciseById, CreateExercise, UpdateExercise } from '../../services/ExerciseService';
import LoadingSpinner from '../loadingSpinner';

export class ExerciseForm extends Component {
  constructor(props) {
    super(props);
    const exerciseId = props.match.params.id;
    const locationState = props.location.state;
    let workoutId = 0;
    let workoutName = '';
    let groupId = 0;
    if(locationState !== undefined) {
      workoutId = locationState.workoutId ? locationState.workoutId : '';
      workoutName = locationState.workoutName ? locationState.workoutName : '';
      groupId = locationState.groupId ? locationState.groupId : '';
    }

    this.state = {
      id: exerciseId,
      name: '',
      weight: '',
      set: '',
      workoutId: workoutId,
      workoutName: workoutName,
      groupId: groupId,
      exerciseOrder: '',
      formSubmitted: false,
      formSuccess: true
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount(){
    if(this.state.id !== undefined) {
      this.getExerciseById(this.state.id);
    }
  }

  async getExerciseById(id) {
    if (id.length > 0) {
      this.setState({ loading: true });
      try {
        const data = await RetrieveExerciseById(id);
        this.setState({
          id: data.id ? data.id : '',
          name: data.name ? data.name : '',
          weight: data.weight ? data.weight : '',
          set: data.set ? data.set : '',
          workoutId: data.workoutId ? data.workoutId : '',
          workoutName: data.workoutName ? data.workoutName : '',
          groupId: data.groupId ? data.groupId : '',
          exerciseOrder: data.exerciseOrder ? data.exerciseOrder : '',
          loading: false,
        });
      } catch(err) {
        this.setState({
          error: true,
          errorMessage: `There was an error retrieving exercise ${id}, please try again.`,
          loading: false
        });
      }
    }
  }

  async storeExercise() {
    this.setState({ loading: true });
    if(this.state.id === undefined || this.state.id.length < 1) {
      try {
        await CreateExercise(this.exerciseFromState());
        this.setState({
          id: this.state.id,
          name: this.state.name,
          weight: this.state.weight,
          set: this.state.set,
          formSubmitted: true,
          formSuccess: true,
          loading: false,
        });
      } catch(err) {
        this.setState({
          error: true,
          errorMessage: `There was an error creating exercise ${this.state.name}, please try again.`,
          loading: false
        });
      }
    } else {
      try {
        await UpdateExercise(this.exerciseFromState());
        this.setState({
          id: this.state.id,
          name: this.state.name,
          weight: this.state.weight,
          set: this.state.set,
          formSubmitted: true,
          formSuccess: true,
          loading: false
        });
      } catch(err) {
        this.setState({
          error: true,
          errorMessage: `There was an error updating exercise ${this.state.name}, please try again.`,
          loading: false
        });
      }
    }
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.name;

    this.setState({
      [name]: target.value
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    this.storeExercise();
  }

  exerciseFromState() {
    const exercise = {
      id: this.state.id,
      name: this.state.name,
      weight: this.state.weight,
      set: this.state.set,
      workoutId: this.state.workoutId,
      workoutName: this.state.workoutName,
      groupId: this.state.groupId,
      exerciseOrder: this.state.exerciseOrder,
    }

    return exercise;
  }

  render() {
    let data;
    if (this.state.formSubmitted && this.state.formSuccess &&
      this.props.location.referrer !== undefined && this.props.location.referrer.length > 0) {
      const url = this.props.location.referrer;
      return (
        <Redirect push to={url} />
      )
    } else if(this.state.loading) {
      data = <LoadingSpinner type='spin' color='green' />
    } else if(this.state.error) {
      data = <div className="alert alert-danger" role="alert">{this.state.errorMessage}</div>
    } else {
      data = <form onSubmit={this.handleSubmit}>
        <div className="form-group">
          <label htmlFor="name">Name:</label>
          <input type="text" name="name" className="form-control" value={this.state.name} onChange={this.handleInputChange} />
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="weight">Weight:</label>
            <input type="text" name="weight" className="form-control" value={this.state.weight} onChange={this.handleInputChange} />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="set">Set:</label>
            <input type="text" name="set" className="form-control" value={this.state.set} onChange={this.handleInputChange} />
          </div>
        </div>
        <input type="submit" value="Save" disabled={!this.state.name} className="btn btn-primary" />
        <input type="button" value="Cancel" className="btn btn-secondary"
            onClick={this.props.history.goBack}/>
      </form>
    }
    return (
      <div>
        {data}
      </div>
    );
  }
}



