import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';

import ExerciseList from '../exercise/exercise';
import { RoundedHeading } from '../roundedHeading';
import { RetrieveWorkoutById } from '../../services/WorkoutService';
import { DeleteExercise } from '../../services/ExerciseService';
import { SessionRoute, WorkoutListRoute, NewExerciseRoute } from '../../services/RouteService';
import LoadingSpinner from '../loadingSpinner';

const Scroll = require('react-scroll');
const scroller = Scroll.scroller;

export class Workout extends Component {
  constructor(props) {
    super(props);
    this.workoutId = props.id;
    if (this.workoutId === undefined || this.workoutId < 1) {
      this.workoutId = props.match.params.id;
    }
    this.sessionId = props.sessionId;

    this.state = {
      workout: [],
      loading: true,
      editMode: false,
      error: false,
      addExercise: false,
      addGroupId: 0,
    }

    this.handleEditClick = this.handleEditClick.bind(this);
    this.handleRemoveClick = this.handleRemoveClick.bind(this);
    this.handleAddClick = this.handleAddClick.bind(this);
  }

  componentDidMount(){
    this.getWorkoutById(this.workoutId);
  }

  async getWorkoutById(id) {
    this.setState({ loading: true, error: false });
    try {
      const data = await RetrieveWorkoutById(id);
      this.setState({workout: data, loading: false});
      this.scrollToGroup();
    } catch(err) {
      this.setState({
        error: true,
        errorMessage: 'An error occurred while loading workout data, please try again.',
        loading: false
      });
    }
  }

  handleEditClick(event) {
    event.preventDefault();
    this.setState({editMode: !this.state.editMode});
  }

  async handleRemoveClick(id) {
    this.setState({ loading: true, error: false });
    try {
      await DeleteExercise(id);
      this.setState({ loading: false });
      this.getWorkoutById(this.workoutId);
    } catch(err) {
      this.setState({
        error: true,
        errorMessage: `An error occurred while removing exercise with id ${id}, please try again.`,
        loading: false
      });
    }
  }

  handleAddClick(groupId) {
    this.setState({
      addExercise: !this.state.addExercise,
      addGroupId: groupId,
    });
  }

  referrer(groupId) {
    let referrerPath = `${WorkoutListRoute()}/${this.state.workout.id}`;
    if (this.sessionId !== undefined && this.sessionId.length > 0) {
      referrerPath = `${SessionRoute()}${this.sessionId}`;
    }
    return `${referrerPath}#${groupId}`;
  }

  scrollToGroup = () => {
    let groupToScrollTo = '';
    if(this.props.location && this.props.location.hash) {
      groupToScrollTo = this.props.location.hash;
    }
    if(this.props.scrollToGroup && this.props.scrollToGroup.length > 0) {
      groupToScrollTo = this.props.scrollToGroup;
    }
    if(groupToScrollTo && groupToScrollTo.length > 1) {
      const group = `group${groupToScrollTo.substr(1)}`;
      scroller.scrollTo(group, {
        duration: 500,
        smooth: true,
        offset: -55
      });
    }
  }

  render() {
    if (this.state.addExercise) {
      const url = {
        pathname: NewExerciseRoute(),
        referrer: this.referrer(this.state.addGroupId),
        state: {
          workoutId: `${this.state.workout.id}`,
          workoutName: `${this.state.workout.name}`,
          groupId: `${this.state.addGroupId}`,
        }
      };

      return <Redirect push to={url} />
    }
    let data;
    if(this.state.loading) {
      data = <LoadingSpinner type='spin' color='green' />
    } else if(this.state.error) {
      data = <div className="alert alert-danger" role="alert">
              {this.state.errorMessage}
            </div>
    } else {
      data = <div className="my-3 p-3 bg-white rounded shadow-sm">
        <h6 className="border-bottom border-gray pb-2 mb-0"><Link to='/workouts'>Back to Workouts</Link></h6>
        <WorkoutGroupList
          workoutId={this.state.workout.id}
          sessionId={this.sessionId}
          groups={this.state.workout.exercises}
          editMode={this.state.editMode}
          handleRemoveClick={this.handleRemoveClick}
          handleAddClick={this.handleAddClick}
        />
      </div>
    }
    return (
      <div>
        <RoundedHeading
          headingText={this.state.workout.name}
          editable={true}
          handleEditClick={this.handleEditClick}
          editMode={this.state.editMode}
        />
        {data}
      </div>
    );
  };
}

const WorkoutGroupList = (props) => {
  const groups = props.groups.sort((a, b) => {
    if(a.name.toUpperCase() < b.name.toUpperCase()) return -1;
    if(b.name.toUpperCase() < a.name.toUpperCase()) return 1;
    return 0;
  });
  const listItems = groups.map((group) =>
    <div id={'group' + group.id} key={group.id} className="media text-muted pt-3">
      <div className="media-body pb-3 mb-0 lh-125 border-bottom border-gray">
        <strong className="d-block text-gray-dark border-bottom">{group.name}</strong>
        <ExerciseList
          workoutId={props.workoutId}
          sessionId={props.sessionId}
          exercises={group.exercises}
          editMode={props.editMode}
          handleRemoveClick={props.handleRemoveClick}
        />
        { props.editMode &&
          <button className="btn btn-secondary" onClick={() => props.handleAddClick(group.id)}>Add Exercise</button>
        }
      </div>
    </div>
  );

  return (
    <div>
      {listItems}
    </div>
  );
};



