import * as d3 from 'd3';

const defaultFillColor = '#66ffaf';

const BarChart = (dataset) => {
    //example dataset
    // var dataset = [80, 100, 56, 120, 180, 30, 40, 120, 160];

    const svgWidth = 500, svgHeight = 300, barPadding = 5;
    const barWidth = (svgWidth / dataset.length);

    const svg = d3.select('svg')
        .attr('width', svgWidth)
        .attr('height', svgHeight);
       
    const barChart = svg.selectAll('rect')
        .data(dataset)
        .enter()
        .append('rect')
        .style('fill', defaultFillColor)
        .attr('y', function(d) {
            return svgHeight - d 
        })
        .attr('height', function(d) { 
            return d; 
        })
        .attr('width', barWidth - barPadding)
        .attr('transform', function (d, i) {
            var translate = [barWidth * i, 0]; 
            return 'translate('+ translate +')';
        });
}

const DateLineChart = (data, selectorClass, leftAxisLabel) => {
    // example data
    // const data = [
    //     { date: new Date(), value: 234 },
    //     { date: new Date(), value: 456 }
    // ]

    var svgWidth = 600, svgHeight = 400;
    var margin = { top: 20, right: 20, bottom: 30, left: 50 };
    var width = svgWidth - margin.left - margin.right;
    var height = svgHeight - margin.top - margin.bottom;

    var svg = d3.select(`.${selectorClass}`)
        .attr('width', svgWidth)
        .attr('height', svgHeight);
        
    var g = svg.append('g')
        .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')');

    var x = d3.scaleTime()
        .rangeRound([0, width]);

    var y = d3.scaleLinear()
        .rangeRound([height, 0]);

    var line = d3.line()
        .x(function(d) { return x(d.date)})
        .y(function(d) { return y(d.value)})
        x.domain(d3.extent(data, function(d) { return d.date }));
        y.domain(d3.extent(data, function(d) { return d.value }));

    g.append('g')
        .attr('transform', 'translate(0,' + height + ')')
        .call(d3.axisBottom(x))
        .select('.domain')
        .remove();

    g.append('g')
        .call(d3.axisLeft(y))
        .append('text')
        .attr('fill', '#000')
        .attr('transform', 'rotate(-90)')
        .attr('y', 6)
        .attr('dy', '0.71em')
        .attr('text-anchor', 'end')
        .text(leftAxisLabel);

    g.append('path')
        .datum(data)
        .attr('fill', 'none')
        .attr('stroke', defaultFillColor)
        .attr('stroke-linejoin', 'round')
        .attr('stroke-linecap', 'round')
        .attr('stroke-width', 1.5)
        .attr('d', line);
}

export { BarChart, DateLineChart };


